@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap);
:root {
  --orange: #ec9412;
  --red: #cc3a3b;
  --skin-color: #fb839e;
  --blue: #2eb1ed;
  --green: #1fc586;
}

:root {
  --bg-black-900: #000000;
  --bg-black-100: #dddddd;
  --bg-black-50: #eff0f4;
  --bg-opacity: rgba(255, 255, 255, 0.5);
  --text-black-900: #000000;
  --text-black-700: #555555;
  --text-black-600: #666666;
  --text-black-300: #bbbbbb;
  --outer-shadow: 3px 3px 3px #d0d0d0, -3px -3px 3px #f8f8f8;
  --outer-shadow-0: 0 0 0 #d0d0d0, 0 0 0 #f8f8f8;
  --inner-shadow: 3px 3px 5px #d0d0d0, inset -3px -3px 3px #f8f8f8;
  --inner-shadow-0: inset 0 0 0 #d0d0d0, inset 0 0 0 #f8f8f8;
}

.dark {
  --bg-black-900: #ffffff;
  --bg-black-100: #353535;
  --bg-black-50: #2b2c2f;
  --bg-opacity: rgba(43, 44, 47, 0.5);
  --text-black-900: #ffffff;
  --text-black-700: #ffffff;
  --text-black-600: #bbbbbb;
  --outer-shadow: 3px 3px 3px #222327, -3px -3px 3px #363636;
  --outer-shadow-0: 0 0 0 #222327, 0 0 0 #363636;
  --inner-shadow: 3px 3px 5px #222327, inset -3px -3px 3px #363636;
  --inner-shadow-0: inset 0 0 0 #222327, inset 0 0 0 #363636;
  background-color: #eff0f4;
  background-color: var(--bg-black-50);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

*:not(i) {
  font-family: 'Raleway', sans-serief;
  font-weight: 400;
}

::before,
::after {
  box-sizing: border-box;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #fb839e;
  background: var(--skin-color);
}

a {
  text-decoration: none;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

.App {
  background-color: #eff0f4;
  background-color: var(--bg-black-50);
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}

.outer-shadow {
  box-shadow: 3px 3px 3px #d0d0d0, -3px -3px 3px #f8f8f8;
  box-shadow: var(--outer-shadow);
}

.inner-shadow {
  box-shadow: 3px 3px 5px #d0d0d0, inset -3px -3px 3px #f8f8f8;
  box-shadow: var(--inner-shadow);
}

.hover-in-shadow {
  position: relative;
  z-index: 1;
}

.hover-in-shadow:hover {
  box-shadow: 0 0 0 #d0d0d0, 0 0 0 #f8f8f8;
  box-shadow: var(--outer-shadow-0);
}

.hover-in-shadow:hover::after {
  box-shadow: 3px 3px 5px #d0d0d0, inset -3px -3px 3px #f8f8f8;
  box-shadow: var(--inner-shadow);
}

.hover-in-shadow::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
}

.btn-1 {
  padding: 15px 25px;
  font-size: 16px;
  font-weight: 500;
  color: #fb839e;
  color: var(--skin-color);
  background-color: transparent;
  line-height: 1.5;
  cursor: pointer;
  border-radius: 30px;
  transition: all 0.3s ease;
  display: inline-block;
}

.btn-1::after {
  border-radius: 30px;
}

/* nav start  */

nav {
  width: 100%;
  background-color: #eff0f4;
  background-color: var(--bg-black-50);
  position: fixed;
  z-index: 1;
  line-height: 90px;
  height: 90px;
}

nav .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav .navbar .navbar__logo a {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: #fb839e;
  color: var(--skin-color);
  border: 2px solid #fb839e;
  border: 2px solid var(--skin-color);
  border-radius: 50%;
  text-align: center;
}

nav .navbar .navbar__list {
  display: flex;
  list-style-type: none;
}

.navbar .navbar__list .navbar__item {
  margin-right: 25px;
}

.navbar .navbar__list .navbar__item .navbar__link {
  padding: 5px 15px;
  font-size: 20px;
  font-weight: 500;
  background-color: transparent;
  color: #666666;
  color: var(--text-black-600);
  line-height: 1.5;
  cursor: pointer;
  text-transform: capitalize;
  border-radius: 30px;
  transition: all 0.3s ease;
  display: inline-block;
}

.navbar .navbar__list .navbar__item .navbar__link:hover {
  color: #fb839e;
  color: var(--skin-color);
}

.navbar .navbar__list .navbar__item .navbar__link::after {
  border-radius: 30px;
}

/* home start  */
.home {
  min-height: 100vh;
  position: relative;
  padding-top: 110px;
  border-bottom: 1px solid #000;
  padding-bottom: 100px;
}

.home__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.home__text h3 {
  font-size: 30px;
  line-height: 40px;
  text-transform: capitalize;
  font-weight: 700;
  color: #fb839e;
  color: var(--skin-color);
  margin-bottom: 8px;
}

.home__text h2,
.home__text h2 span {
  font-size: 40px;
  line-height: 40px;
  text-transform: capitalize;
  font-weight: 700;
  margin-bottom: 25px;
}

.home__text h2 {
  color: #000000;
  color: var(--bg-black-900);
}

.home__text h2 span {
  color: #fb839e;
  color: var(--skin-color);
}

.home__img {
  border: 12px solid #fb839e;
  border: 12px solid var(--skin-color);
  border-radius: 50%;
  margin-top: 30px;
}

.img-box {
  border: 7px solid #dddddd;
  border: 7px solid var(--bg-black-100);
  width: 450px;
  max-width: 100%;
  border-radius: 50%;
  overflow: hidden;
  /* background-color: red; */
}

.effect-wrap .effect {
  position: absolute;
}

.effect-1 {
  width: 30px;
  height: 30px;
  border: 4px solid #8a49ff;
  right: 5%;
  bottom: 9%;
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
}

.effect-2 {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  right: 5%;
  top: 30%;
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
}

.effect-2 div {
  margin: 1px;
  width: 100%;
  height: 3px;
  background-color: #4dd0e1;
}

.effect-3 {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 25px solid #fb839e;
  border: 25px solid var(--skin-color);
  right: 40%;
  top: -3%;
  -webkit-animation: leftBounce 3s linear infinite;
          animation: leftBounce 3s linear infinite;
}

.effect-4 {
  width: 40px;
  height: 40px;
  top: 55%;
  left: 3%;
  display: flex;
  flex-wrap: wrap;
  -webkit-animation: topBounce 3s linear infinite;
          animation: topBounce 3s linear infinite;
}

.effect-4 div {
  width: 3px;
  height: 3px;
  background-color: #ff9c07;
  margin: 0 3px 8px;
}

.effect-5 {
  /* border: 30px solid #000; */
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-bottom: 25px solid #06d79c;
  left: 30%;
  top: 30%;
  -webkit-animation: spin 15s linear infinite;
          animation: spin 15s linear infinite;
}

.effect-5::before {
  content: '';
  position: absolute;
  top: -8px;
  left: -25px;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-bottom: 25px solid #06d79c;
  opacity: 0.5;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes leftBounce {
  0%,
  100% {
    left: 50%;
  }
  50% {
    left: 48%;
  }
}

@keyframes leftBounce {
  0%,
  100% {
    left: 50%;
  }
  50% {
    left: 48%;
  }
}

@-webkit-keyframes topBounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(25px);
  }
}

@keyframes topBounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(25px);
  }
}

/* About start */

.about {
  margin-top: 100px;
  min-height: 110vh;
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}

.about__img {
  position: relative;
  flex: 0 0 40%;
  margin-top: -30px;
  max-width: 100%;
  width: 40%;
  margin: 0 15px;
  border: 12px solid #fb839e;
  border: 12px solid var(--skin-color);
}

.about__img .about__img-box {
  border: 7px solid #dddddd;
  border: 7px solid var(--bg-black-100);
}

.about__img .social-links {
  position: absolute;
  bottom: -70px;
  left: 80px;
}

.about__img .social-links a {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  margin: 0 10px 0;
  color: #666666;
  color: var(--text-black-600);
  transition: all 0.3s ease;
}

.about__img .social-links a::after {
  border-radius: 50%;
}

.about__img .social-links a:hover {
  color: #fb839e;
  color: var(--skin-color);
}

.about__description {
  width: 60%;
  max-width: 100%;
  max-width: 100%;
  flex: 0 0 60%;
  padding: 0 15px;
}

.about__description span {
  font-weight: 700;
  color: #666666;
  color: var(--text-black-600);
}

.about__description p {
  margin-bottom: 20px;
  line-height: 26px;
  color: #666666;
  color: var(--text-black-600);
}

.about__description a {
  margin-right: 20px;
}

.about__description .odometrs {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  width: 500px;
  max-width: 100%;
  height: 50px;
}

.about__description .odometrs .odometr {
  width: 100px;
  height: 100px;
  line-height: 100px;
  border-radius: 50%;
  text-align: center;
}

.about__description .odometrs .odometr span {
  font-size: 30px;
  color: #fb839e;
  color: var(--skin-color);
}

.about .about__info {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 60px;
}

.about .about__info span {
  display: inline-block;
  color: #fb839e;
  color: var(--skin-color);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

.about .about__info h2 {
  font-size: 30px;
  font-weight: 700;
  margin: 0;
  color: #555555;
  color: var(--text-black-700);
}

.style-switcher {
  position: fixed;
  right: 0;
  top: 77px;
  padding: 15px;
  width: 200px;
  z-index: 101;
  border-radius: 5px;
  transition: all 0.3s ease;
  transform: translateX(100%);
}

.style-switcher.open {
  transform: translateX(-15px);
}

.style-switcher .s-icon {
  position: absolute;
  height: 40px;
  width: 40px;
  text-align: center;
  font-size: 20px;
  color: var(--bg-black-900);
  right: 100%;
  margin-right: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 50%;
}

.style-switcher .s-icon i {
  line-height: 40px;
}

.style-switcher .s-icon::after {
  border-radius: 50%;
}

.style-switcher .style-switcher-toggler {
  top: 0;
}

.style-switcher .day-night {
  top: 55px;
}

.style-switcher h4 {
  margin: 0 0 10px;
  color: var(--text-black-700);
  font-size: 16px;
  font-weight: 600;
  text-transform: capitalize;
}

.style-switcher .colors {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.style-switcher .colors span {
  display: inline-block;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  cursor: pointer;
}

.style-switcher .color-1 {
  background-color: #fb839e;
}
.style-switcher .color-2 {
  background-color: #ec9412;
}
.style-switcher .color-3 {
  background-color: #1fc586;
}
.style-switcher .color-4 {
  background-color: #2eb1ed;
}
.style-switcher .color-5 {
  background-color: #cc3a3b;
}

